@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
   
}
body{
    background:linear-gradient(to right, #7d0633 , #31112c) ;
}
.Container{
    width: 90%;
    margin: 0 auto;
}

.header img{
    margin: 30px 0;
}

@media only screen and (max-width: 500px)
{
    .Container img{
        width: 300px;
    }
    
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.container{
    text-align: center;
    width: 250px;
    background-color:  #9eebcf;
    padding: 30px;
    margin: 10px;
    border-radius: 10px;
    transition: all 0.4s;
    font-family: 'Roboto', sans-serif;
}

.container img{
    padding: 5px;
    width: 220px;
}

.container h2{
    padding: 5px;
}

@media only screen and (max-width: 500px)
{
    
    .container{
        width: 180px;
    }
    .container img{
        width: 121px;
    }
    .container h2{
        font-size: 0.9em;
    }
    .container p{
        font-size: 0.7em;
    }
}

@media only screen and (max-width: 400px)
{
    
    .container{
        width: 110px;
    }
    .container img{
        width: 120px;
    }
    .container h2{
        font-size: 0.9em;
    }
    .container p{
        font-size: 0.5em;
    }
}
